import { Component, OnInit, ViewChild } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { NgForm, NgModel, NgControl, FormsModule } from '@angular/forms';
import { LoopBackAuth, BooksApi, ContainerApi, CityApi, Equipments_masterApi } from '../../../../shared/loopback-sdk';
import { CookieService } from 'angular2-cookie/core';
import { Headers, Http, Response } from '@angular/http';
import { stringify } from 'querystring';
import { ShareService } from '../../../../providers/share-service/share-service';
declare var window: any;
declare var hostname: any;
declare var sdkPrefix;
@Component({
	templateUrl: 'edit-equipment.component.html'
})
export class EditEquipmentComponent {
	public id: number;
	display: boolean = false; result: any;
	submitted: boolean = false;
	equipmentName: any;
	busy: any;
	userDetails: any;
	rolename: any;
	loading: boolean = false;
	equipmentStatus:boolean = false;
	bookImage:any;
	loginFlagToken:any;
	contractorCount : number = 0;
	municipalityCount : number = 0;
	equipmentId : number = 0;
	equipmentPreviousStatus : boolean = false;
	

	editEquipment={
		"equipment_id":0,
		"equipment_name":"",
		"status": 0,
		//"updated_date":0
	}
	constructor(private loopAuth: LoopBackAuth, private route: ActivatedRoute, private http: Http, private _cookieService: CookieService, private books: BooksApi, private router: Router, private containerApi: ContainerApi, private share: ShareService, private cityApi:CityApi,private equipmentApi:Equipments_masterApi) {
		this.userDetails = this.loopAuth.getCurrentUserData();
		this.rolename = this.userDetails.userDet.roles;
		this.id = this.route.snapshot.params['id'];
		this.loginFlagToken = this._cookieService.get(sdkPrefix+'id');
	}

	ngOnInit(): void {
		this.loading = true;
		var dataArrObj = {
			"equipment_id":this.id
		}
		this.equipmentApi.getEquipment(dataArrObj).subscribe(data=>{
			
			if(data){
				let result = data.result[0];
				this.equipmentId = result.equipment_id;
				this.equipmentName = result.equipment_name;
				this.equipmentStatus = (result.status)?true:false;
				this.contractorCount = result.total;
				//console.log("contractorCount ",this.contractorCount);
				this.editEquipment={
					"equipment_id":result.equipment_id,
					"equipment_name": result.equipment_name,
					"status": result.status,
					//"updated_date":result.updated_date
				}


			}
			this.loading = false;
		},
		err=>{
			//console.log("err", err);
			this.loading = false;
		},
		()=>{

		});
	}

	goToListing() {
		// window.location.reload();
		this.router.navigate(["equipment"]);
	}

	async submit(equipmentForm) {
		this.submitted = true;
		if (equipmentForm.valid) {
			this.loading = true;

			let date = new Date();
			let currentdate = date.getTime();

			this.editEquipment.equipment_id = this.equipmentId;
			this.editEquipment.equipment_name = this.capital_letter(this.equipmentName);
			this.editEquipment.status = (this.equipmentStatus)?1:0;
			//this.editEquipment.updated_date = currentdate;

			

			//updating equipments-master
		
			// this.ifNameExist(this.editEquipment).then((total)=>{
			// 	if(total > 0){
			// 		this.loading = false;
			// 		this.result = "Equipment name already exist";
			// 		this.display = true;
			// 	}else{
			// 		console.log("this.editEquipment ",this.editEquipment);
			this.checkCount(this.editEquipment).then((total)=>{
				if(total > 0 && !this.editEquipment.status){
					this.loading = false;
					this.result = "Equipment is already been used so it cannot be inactive";
					this.display = true;
				}else{
					this.equipmentApi.upsertPatch(this.editEquipment).subscribe((data)=>{
						this.loading = false;
						this.result = "success";
						this.display = true;
					},(err)=>{
						this.loading = false;
						this.result = "Something went wrong";
						this.display = true;
					});
				}
			},(err)=>{
					this.loading = false;
					this.result = "Something went wrong";
					this.display = true;
				});
			// 	}
			// },(err)=>{
			// 	this.loading = false;
			// 	this.result = "Something went wrong";
			// 	this.display = true;
			// });

			
		}
	}

	ifNameExist(dataObj){
		return new Promise((resolve,reject)=>{
			try{
				let access_token = this.loopAuth.getAccessTokenId();
				let equipmentString : string = dataObj.equipment_name
				let cond = { "equipment_name": equipmentString.toString()};

				let api="/api/equipments_masters/count?where=";
				
				let url = hostname+api+encodeURI(JSON.stringify(cond))+"&access_token="+access_token;
				this.http.get(url).subscribe((data)=>{
					//console.log("Count ",data.json());
					resolve(data.json().count);
				},(err)=>{
					reject(err);
				});

			}catch(err){
				reject(err);
			}
		});
		
		
	}
	checkCount(dataObj){
		return new Promise((resolve,reject)=>{
			try{
				let access_token = this.loopAuth.getAccessTokenId();
				let equipment_id = dataObj.equipment_id;
				let cond = { "equipment_id": equipment_id};
				
				let api="/api/equipments_mappings/count?where=";
				
				let url = hostname+api+encodeURI(JSON.stringify(cond))+"&access_token="+access_token;
				this.http.get(url).subscribe((data)=>{
					//console.log("Count ",data.json());
					resolve(data.json().count);
				},(err)=>{
					reject(err);
				});
			}
			catch(err){
				reject(true);
			}
		});
	}

	capital_letter(str) 
	{
		str = str.split(" ");

		for (var i = 0, x = str.length; i < x; i++) {
			str[i] = str[i][0].toUpperCase() + str[i].substr(1);
		}

		return str.join(" ");
	}

	
}


